<template>
  <!-- 内容区 -->
  <div class="firstPage">
    <home>
      <main class="main">
        <el-row type="flex" justify="center">
          <el-col :span="18" class="main_content">
            <el-card>
              <el-row type="flex" justify="center">
                <el-col :span="18">
                  <div class="dream">
                    <div class="title center-align">
                      <i class="el-icon-s-opportunity"></i>&nbsp;&nbsp;我的梦想
                    </div>

                    <div class="text">
                      不是每个人都应该像我这样去建造一座水晶大教堂，但是每个人都应该拥有自己的梦想，设计自己的梦想，追求自己的梦想，实现自己的梦想。梦想是生命的灵魂，是心灵的灯塔，是引导人走向成功的信仰。有了崇高的梦想，只要矢志不渝地追求，梦想就会成为现实，奋斗就会变成壮举，生命就会创造奇迹。——罗伯·舒乐
                    </div>
                  </div>

                  <div class="video">
                    <div class="title center-align">
                      <i class="el-icon-video-camera-solid"></i>&nbsp;&nbsp;
                      精彩视频
                    </div>
                    <div id="dplayer"></div>
                  </div>

                  <div class="article">
                    <div class="title center-align">
                      <i class="el-icon-date"></i>&nbsp;&nbsp; 推荐文章
                    </div>
                    <div class="full">
                      <img
                        class="full"
                        :src="staticUrl+'/static/img/artcle/jia.jpg'"
                        alt=""
                        srcset=""
                      />
                      <p class="text_mi">
                        杭州有个山寺,挂着一副门联:南来北往,有多少人忙忙;爬高走低,何不停下坐坐。坐下做甚?喝茶呀。天下便到处都有了茶庄。西京城里也就开着一家,名字叫暂坐...
                      </p>
                    </div>
                  </div>
                </el-col>
              </el-row>
            </el-card>
          </el-col>
        </el-row>
      </main>
    </home>
  </div>
</template>
<script>
import DPlayer from "dplayer";
import home from "../home/home.vue";

export default {
  name: "firstpage",
  components: { home },
  data() {
    return {
      staticUrl:this.$staticUrl
    };
  },
  created() {
    this.$nextTick(() => {
      this.video(); // 视频
    });
  },
  methods: {
    // 初始化视频
    video() {
      const dp = new DPlayer({
        container: document.getElementById("dplayer"),
        autoplay: false,
        hotkey: true,
        preload: "auto",
        video: {
          url: "https://blinkfox.github.io/medias/videos/demo.mp4",
          pic: "https://t1.picb.cc/uploads/2021/04/04/ZBlUGj.jpg",
        },
      });
      // dp.seek(1);
    },
  },
};
</script>
<style lang="less" scoped>
// 内容区
.main {
  min-height: 772px;
  margin: 10px;
  .title {
    margin-bottom: 20px;
    font-size: 2rem;
    font-weight: 700;
  }
  .main_content {
    .dream {
      margin-top: 20px;
      margin-bottom: 40px;
      background: white;
    }
  }
}
</style>


